@use "../utils/functions/"as*;
@use "../utils/mixins/"as*;

.site-container {
  overflow-x: clip;
}

.hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.sr-only {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.yellow {
  clip-path: inset(0 -100vmax);
  box-shadow: 0 0 0 100vmax var(--color-yellow-300);
  background-color: var(--color-yellow-300);
}

.green {
  clip-path: inset(0 -100vmax);
  box-shadow: 0 0 0 100vmax var(--color-green-300);
  background-color: var(--color-green-300);
}

.list {
  padding-left: 37px;

  li {
    list-style: disc;
    line-height: 1;

    &:not(:last-child) {
      margin-bottom: 6px;
    }
  }
}